import { AssignmentOutlined } from "@mui/icons-material";
import DeleteIcon from "@mui/icons-material/DeleteOutline";
import MoreIcon from "@mui/icons-material/MoreHoriz";
import PrintIcon from "@mui/icons-material/PrintOutlined";
import { Box, Button, Card, CardContent, CardHeader, Divider, Grid, IconButton, Link, ListItemIcon, ListItemText, Menu, MenuItem, Stack, Tab, Tabs } from "@mui/material";
import { useEffect, useMemo, useState } from "react";
import { Outlet, useLocation, useNavigate, useParams } from "react-router-dom";
import { useMiddleware, useTask } from "../../../../Application";
import { useAuth } from "../../../../auth";
import DataList from "../../../../components/DataList";
import DetailedDate from "../../../../components/DetailedDate";
import { TabView } from "../../../../tabs";
import pricify from "../../../../utils/pricify";
import useDialog from "../../../../utils/useDialog";
import InvoiceDeleteConfirmation from "../../components/InvoiceDeleteConfirmation";
import InvoicePrintDialog from "../../components/InvoicePrintDialog";
import isFinite from "lodash/isFinite";
import Utils from "../../Utils";


const tour = {
    key: 'ceremonies/reservation-invoice-details',
    steps: [{
        title: "جزئیات صورت‌حساب",
        intro: "در این فرآیند شما می‌توانید جزئیات صورت‌حساب را مشاهده نمایید. در ادامه با بخش‌های مختلف آشنا خواهید شد.",
    }, {
        title: "مشخصات عمومی",
        intro: "در این قسمت می‌توانید مشخصات عمومی صورت‌حساب را مشاهده نمایید.",
        element: ".invoice-general",
    }, {
        title: "مشخصات رزرو",
        intro: "در این قسمت می‌توانید مشخصات و عملیات مرتبط با رزرو و کرایه مثل تحویل بار و برگشت صورت‌حساب را مشاهده نمایید.",
        element: ".invoice-reserve",
    }, {
        title: "فاکتور",
        intro: "با کلیلک بر روی این دکمه می‌توانید اقلام، خالص و مجموع فاکتور را مشاهده نمایید.",
        element: ".invoice-bill",
    }, {
        title: "رویدادهای مالی",
        intro: "با کلیک بر روی این دکمه می‌توانید تاریخچه‌ای از تمام رویدادهای مالی مرتبط با صورت‌حساب را مشاهده و پرداخت نمایید.",
        element: ".invoice-transactions",
    }, {
        title: "مشخصات مشتری",
        intro: "در این قسمت می‌توانید مشخصات مشتری در صورت‌حساب را مشاهده نمایید.",
        element: ".invoice-customer",
    }, {
        title: "امور مالی",
        intro: "در این قسمت می‌توانید مشخصات مالی مرتبط با صورت‌حساب را مشاهده نمایید و پرداخت جدیدی برای صورت‌حساب ثبت کنید و یا صورت‌حساب را تسویه‌حساب کامل کنید..",
        element: ".invoice-transactions-overview",
    }, {
        title: "ارسال و باربری",
        intro: "در این قسمت می‌توانید لیست سرویس‌های باربری صورت‌حساب و همچنین آدرس تحویل بار را مشاهده نمایید و یا سرویس‌ها را تغییر بدهید و به راننده اختصاص دهید.",
        element: ".invoice-shipments",
    }, {
        title: "نوار عملیات",
        intro: "در این بخش می‌توانید به عملیات مختلفی برای صورت‌حساب دسترسی داشته باشد. عملیاتی مانند چاپ، ویرایش و حذف",
        element: ".invoice-actions",
    }]
};



const InvoiceDetails = () => {

    const navigate = useNavigate();
    const location = useLocation();
    const params = useParams();

    const { inject } = useAuth();
    const middleware = useMiddleware('invoices.main');


    const [loading, setLoading] = useState(false);
    const [invoice, setInvoice] = useState({});

    const [anchorEl, setAnchorEl] = useState(null);

    const _loadInvoiceAsync = async () => {
        setLoading(true);
        const res = await middleware.details(params.invoiceId);

        if (res.ok) {
            setInvoice(res.invoice);
            setLoading(false);
        }
    }

    useEffect(() => {
        _loadInvoiceAsync();
    }, [params.invoiceId]);


    const {
        isOpen: isPrintDialogOpen,
        isOpening: isPrintDialogOpening,
        open: openPrintDialog,
        close: closePrintDialog,
        data: printDialogInvoice
    } = useDialog();

    const {
        isOpen: isDeleteDialogOpen,
        isOpening: isDeleteDialogOpening,
        open: openDeleteDialog,
        close: closeDeleteDialog,
        data: deleteDialogInvoice
    } = useDialog();

    const _injections = inject('invoices', 'InvoiceDetails.State');
    const [injections, setInjections] = useState(
        _injections ? Object.assign({},
            ..._injections
        ) : {}
    )

    const total = useMemo(() => invoice && invoice.items ? Utils.getInvoiceTotal(invoice.items, invoice.factors) : 0, [invoice])

    const task = useTask('Invoices.details')

    return (
        <TabView
            loading={loading}
            {...Object.assign(
                {},
                task(invoice),
                ...(inject('Invoices', 'InvoiceDetails.TabView', { loading, invoice }) || [])
            )}
        >
            <Box sx={{ p: { xs: 1, sm: 2, md: 3 } }}>
                <Box sx={{ display: "flex", mb: 1 }}>
                    <Box sx={{ flexGrow: 1 }} />
                    <Box className="invoice-actions">
                        <Stack direction="row" spacing={0.5}>
                            <Button
                                variant="transparent"
                                startIcon={<PrintIcon />}
                                onClick={() => openPrintDialog(invoice)}
                                disabled={isPrintDialogOpening}
                            >چاپ</Button>
                            <IconButton
                                onClick={e => setAnchorEl(e.currentTarget)}
                            >
                                <MoreIcon />
                            </IconButton>
                        </Stack>
                    </Box>
                </Box>
                <Grid container spacing={2} alignItems="flex-start">
                    <Grid item xs={12} md={3} container spacing={2}>
                        <Grid item xs={12}>
                            <Card variant="outlined" className="invoice-general">
                                <CardHeader title="صورت‌حساب" titleTypographyProps={{ variant: "h6" }} />
                                <CardContent>
                                    <DataList
                                        data={
                                            [{
                                                title: 'شماره',
                                                description: invoice.number
                                            }, {
                                                title: 'تاریخ ثبت',
                                                description: <DetailedDate date={invoice.issuedAt} />
                                            }, {
                                                title: 'خالص فاکتور',
                                                description: (
                                                    <Box>
                                                        {isFinite(total) ? pricify(total) : 0}
                                                        {` `}
                                                        <small>ریال</small>
                                                    </Box>
                                                )
                                            }, {
                                                title: 'توضیحات',
                                                description: invoice.note
                                            }]
                                        }
                                    />
                                </CardContent>
                            </Card>
                        </Grid>
                        {inject('invoices', 'InvoiceDetails.PreSide', { invoice, state: injections, setState: setInjections, refetch: _loadInvoiceAsync })}
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <Card variant="outlined">
                            <Tabs value={location.pathname}>
                                <Tab
                                    className="invoice-bill"
                                    value={`/invoice/${params.invoiceId}`}
                                    onClick={() => navigate(
                                        `/invoice/${params.invoiceId}`,
                                        { state: { prev: location }, replace: true }
                                    )}
                                    label="فاکتور"
                                />
                                {(inject('invoices', 'InvoiceDetails.TabsList', { invoiceId: params.invoiceId }) || []).map((tab, index) => (
                                    <Tab
                                        key={index}
                                        value={tab.value}
                                        onClick={() => navigate(
                                            tab.value,
                                            { state: { prev: location }, replace: true }
                                        )}
                                        label={tab.label}
                                        className={tab.className}
                                    />
                                ))}
                            </Tabs>
                            <Divider />
                            <Outlet />
                        </Card>
                    </Grid>
                    <Grid item xs={12} md={3} container spacing={2}>
                        <Grid item xs={12}>
                            <Card variant="outlined" className="invoice-customer">
                                <CardHeader title="اطلاعات مشتری" titleTypographyProps={{ variant: "h6" }} />
                                <CardContent>
                                    <DataList
                                        data={
                                            [{
                                                title: 'مشتری',
                                                description: invoice.customer?.fullName
                                            }, {
                                                title: 'شماره‌های تماس',
                                                description: invoice.customer?.fullContact
                                            }]
                                        }
                                    />
                                </CardContent>
                            </Card>
                        </Grid>
                        {inject('invoices', 'InvoiceDetails.PostSide', { invoice, state: injections, setState: setInjections, refetch: _loadInvoiceAsync })}
                    </Grid>
                </Grid>

                <InvoicePrintDialog
                    open={isPrintDialogOpen}
                    onClose={closePrintDialog}
                    invoice={printDialogInvoice}
                />

                <InvoiceDeleteConfirmation
                    open={isDeleteDialogOpen}
                    onClose={closeDeleteDialog}
                    invoice={deleteDialogInvoice}
                />

                {inject('invoices', 'InvoiceDetails.PostFooter', { invoice, state: injections, setState: setInjections, refetch: _loadInvoiceAsync })}

                <Menu
                    open={Boolean(anchorEl)}
                    anchorEl={anchorEl}
                    onClose={() => setAnchorEl(null)}
                >
                    {inject('invoices', 'InvoiceDetails.Menu', {
                        invoice,
                        refetch: _loadInvoiceAsync,
                        menuProps: {
                            setAnchor: setAnchorEl,
                        }
                    })}
                    <MenuItem onClick={() => {
                        setAnchorEl(null);
                        openDeleteDialog(invoice)
                    }}>
                        <ListItemIcon>
                            <DeleteIcon />
                        </ListItemIcon>
                        <ListItemText>حذف</ListItemText>
                    </MenuItem>
                </Menu>

            </Box>
        </TabView>
    )
}

export default InvoiceDetails;