import CloseIcon from "@mui/icons-material/CloseOutlined";
import { Box, Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Divider, IconButton } from "@mui/material";
import { useMiddleware } from "../../../Application";
import { useSnackbar } from "notistack";
import { useNavigate } from "react-router-dom";


const InvoiceDeleteConfirmation = ({ open, onClose, onDelete, invoice }) => {

    const { enqueueSnackbar } = useSnackbar();
    const middleware = useMiddleware('invoices.main');

    const navigate = useNavigate()

    const deleteInvoiceAsync = async () => {
        const { ok } = await middleware.delete(invoice.id);
        if (ok) {
            enqueueSnackbar('صورت‌حساب حذف گردید.');
            // onClose();
            // onDelete();
            navigate(-1);
        }
    }

    if (!invoice) {
        return (null);
    }

    return (
        <Dialog
            fullWidth
            maxWidth="xs"
            open={open}
            onClose={onClose}
        >
            <DialogTitle sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <Box>تایید حذف صورت‌حساب</Box>
                <IconButton onClick={onClose} size="small">
                    <CloseIcon />
                </IconButton>
            </DialogTitle>
            <Divider />
            <DialogContent>
                <DialogContentText id="alert-dialog-description">
                    {[
                        'آیا مطمئنید صورت‌حساب',
                        `شماره ${invoice?.number}`,
                        invoice.client && `بنام ${invoice.client?.fullName}`,
                        'را کنسل و حذف نمایید؟'
                    ].filter(Boolean).join(' ')}
                </DialogContentText>
            </DialogContent>
            <Divider />
            <DialogActions>
                <Button onClick={deleteInvoiceAsync} variant="contained" color="error">تایید و حذف</Button>
                <Box sx={{ flexGrow: 1 }} />
                <Button onClick={onClose} autoFocus>انصراف</Button>
            </DialogActions>
        </Dialog>
    )
}

export default InvoiceDeleteConfirmation