import CloseIcon from "@mui/icons-material/CloseOutlined";
import {
    Box,
    Button, Dialog,
    DialogActions,
    DialogContent,
    DialogTitle, Divider, IconButton, styled, Table, TableBody, TableCell,
    tableCellClasses,
    TableHead, TableRow, Typography
} from "@mui/material";
import format from "date-fns-jalali/format";
// import moment from "moment-jalaali";
import { forwardRef, useEffect, useRef } from "react";
import { useReactToPrint } from 'react-to-print';
import pricify from "../../../utils/pricify";



const StyledTableCell = styled(TableCell)(({ theme }) => ({
    padding: '4px 8px',
    [`&.${tableCellClasses.head}`]: {
        color: theme.palette.common,
        fontSize: '0.5rem',
        borderBottom: '0.5px solid'.concat(theme.palette.common.black),
        borderTop: '0.5px solid'.concat(theme.palette.common.black),
        borderRight: '0.5px solid'.concat(theme.palette.common.black),
        '&:first-of-type': {
            borderLeft: '0.5px solid'.concat(theme.palette.common.black),
        }
    },
    [`&.${tableCellClasses.body}`]: {
        fontSize: '0.5rem',
        fontWeight: 500,
        color: theme.palette.common,
        borderBottom: '0.5px solid'.concat(theme.palette.common.black),
        borderRight: '0.5px solid'.concat(theme.palette.common.black),
        '&:first-of-type': {
            borderLeft: '0.5px solid'.concat(theme.palette.common.black),
        }
    },
}));


const InvoiceContainer = forwardRef(({ invoice }, ref) => {
    return (
        <DialogContent ref={ref} dir="rtl">
            <Box sx={{ p: 0 }}>
                <Box sx={{ mb: 1 }}>
                    <Box sx={{ display: "flex", alignItems: "flex-end", justifyContent: "space-between" }}>
                        <Box>
                            <Typography sx={{ mb: 0.5, display: "block", fontSize: '0.5rem' }} variant="subtitle2">کد مشتری :‌ {invoice.client?.code}</Typography>
                            <Typography sx={{ mb: 0.5, display: "block", fontSize: '0.5rem' }} variant="subtitle2">نام مشتری :‌ {invoice.client?.fullName}</Typography>
                        </Box>
                        <Box sx={{ flexGrow: 1 }}>
                            {/* <Box
                                component='img'
                                sx={{
                                    height: 'auto',
                                    width: 100,
                                    mb: 0.5
                                }}
                                alt="Logo"
                                src={logo}
                            /> */}
                        </Box>
                        <Box>
                            <Typography sx={{ mb: 0.5, display: "block", fontSize: '0.5rem' }} variant="subtitle2">شماره فاکتور :‌ {invoice.number}</Typography>
                            <Typography sx={{ mb: 0.5, display: "block", fontSize: '0.5rem' }} variant="subtitle2">تاریخ فاکتور :‌ {format(new Date(invoice.reservedAt), 'yyyy/MM/dd')}</Typography>
                            <Typography sx={{ mb: 0.5, display: "block", fontSize: '0.5rem' }} variant="subtitle2">مدت کرایه :‌ {invoice.duration} <small>روز</small></Typography>
                        </Box>
                    </Box>
                    {invoice.shippingAddress && <Typography sx={{ mb: 0.5, display: "block", fontSize: '0.5rem' }} variant="subtitle2">آدرس :‌ {invoice.fullShippingAddress}</Typography>}
                    {invoice.description && <Typography sx={{ display: "block", fontSize: '0.5rem' }} variant="subtitle2">توضیحات :‌ {invoice.description}.</Typography>}
                </Box>
                <Table stickyHeader size="small">
                    <TableHead>
                        <TableRow>
                            <StyledTableCell align="center" width={40}>ردیف</StyledTableCell>
                            <StyledTableCell align="center">کد</StyledTableCell>
                            <StyledTableCell>شرح محصول/خدمت</StyledTableCell>
                            <StyledTableCell align="center">تعداد</StyledTableCell>
                            <StyledTableCell align="center">فی</StyledTableCell>
                            <StyledTableCell align="center">تخفیف</StyledTableCell>
                            <StyledTableCell align="center">مجموع</StyledTableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {invoice.items && invoice.items.map((item, index) => (
                            <TableRow key={index}>
                                <StyledTableCell align="center">{index + 1}</StyledTableCell>
                                <StyledTableCell align="center">{item.item.code}</StyledTableCell>
                                <StyledTableCell>{item.item?.title}</StyledTableCell>
                                <StyledTableCell align="center">{item.reservedQuantity}</StyledTableCell>
                                <StyledTableCell align="center">{pricify(item.unitPrice)}</StyledTableCell>
                                <StyledTableCell align="center">{item.discountPercent}</StyledTableCell>
                                <StyledTableCell align="center">{pricify(item.total)}</StyledTableCell>
                            </TableRow>
                        ))}
                        {[1, 2].map((n, i) => (
                            <TableRow key={i}>
                                <StyledTableCell sx={{ px: 0 }} align="center">{(invoice.items || []).length + i + 1}</StyledTableCell>
                                <StyledTableCell sx={{ px: 1 }} align="center"></StyledTableCell>
                                <StyledTableCell></StyledTableCell>
                                <StyledTableCell align="center"></StyledTableCell>
                                <StyledTableCell align="center"></StyledTableCell>
                                <StyledTableCell align="center"></StyledTableCell>
                                <StyledTableCell align="center"></StyledTableCell>
                            </TableRow>
                        ))}

                        <TableRow>
                            <TableCell sx={{ borderRight: '1px solid #000', borderBlockColor: 'transparent' }} colSpan={4} />
                            <StyledTableCell colSpan={2}>جمع کل فاکتور</StyledTableCell>
                            <StyledTableCell align="center">{pricify(invoice.subtotal)}</StyledTableCell>
                        </TableRow>
                        {invoice.factors && invoice.factors.map((factor, index) => (
                            <TableRow key={index}>
                                <TableCell sx={{ borderRight: '1px solid #000', borderBlockColor: 'transparent' }} colSpan={4} />
                                <StyledTableCell colSpan={2}>{factor.title}</StyledTableCell>
                                <StyledTableCell align="center">{pricify(factor.amount)}</StyledTableCell>
                            </TableRow>
                        ))}
                        <TableRow>
                            <TableCell sx={{ borderRight: '1px solid #000', borderBlockColor: 'transparent' }} colSpan={4} />
                            <StyledTableCell colSpan={2}>خالص فاکتور</StyledTableCell>
                            <StyledTableCell align="center"><strong>{pricify(invoice.total)}</strong> <small>ریال</small></StyledTableCell>
                        </TableRow>
                    </TableBody>
                </Table>
                <Box sx={{ py: 3, display: "flex" }}>
                    <Box component={Typography} sx={{ flexGrow: 1, textAlign: 'center', fontSize: '0.5rem' }} variant="subtitle2">امضاء فروشنده</Box>
                    <Box component={Typography} sx={{ flexGrow: 1, textAlign: 'center', fontSize: '0.5rem' }} variant="subtitle2">امضاء خریدار</Box>
                </Box>
                <Box>
                    <Typography sx={{ display: "block", mb: 1, fontSize: '0.675rem' }} variant="subtitle2">شرایط تعیین شده برای اجاره وسایل :</Typography>
                    <Typography sx={{ display: "block", textAlign: "justify", fontSize: '0.5rem' }} variant="body2">
                        ۱. مشتری موظف است ضمن تحویل کارت شناسایی موجه و معتبر و پرداخت کامل هزینه قبل از اجاره لوازم، در هر ساعتی از شبانه‌روز که وسایل  اجاره‌ای خود را تحویل می‌گیرد متعهد شود وسایل مورد اجاره را تا ساعت ۱۰ صبح روز بعد، تحویل ظروف کرایه نماید در غیراینصورت جهت جبران خسارت تاخیر، کرایه مجدد محاسبه می‌گردد.
                    </Typography>
                    <Typography sx={{ mb: 1, display: "block", textAlign: "justify", fontSize: '0.5rem' }} variant="body2">
                        ۲. مشتری موظف است در صورت کنسلی لوازم مورد اجاره، تا ۷۲ ساعت قبل از زمان تحویل اعلان نماید در غیر اینصورت موظف به پرداخت ۳۰ تا ۶۰ درصد و درصورت کنسلی بعد از ۲۴ ساعت مانده به زمان تحویل موظف به پرداخت ۱۰۰ درصد از هزینه اجاره لوازم مورد اجاره می‌گردد.
                    </Typography>
                </Box>
            </Box>
        </DialogContent>
    )
})


const InvoicePrintDialog = ({ open, onClose, invoice }) => {

    const invoiceRef = useRef();
    const handlePrint = useReactToPrint({
        content: () => invoiceRef.current
    })

    const handlePrintViaKey = (event) => {
        if (event.ctrlKey && event.keyCode == 80) {
            handlePrint();
        }
    }

    useEffect(() => {

        window.addEventListener("keydown", handlePrintViaKey);

        return () => {
            window.removeEventListener("keydown", handlePrintViaKey);
        }
    }, []);

    return (
        <Dialog
            open={open}
            onClose={onClose}
            fullWidth
            maxWidth="md"
        >
            <DialogTitle sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <Box>صورتحساب چاپی</Box>
                <IconButton onClick={onClose} size="small">
                    <CloseIcon />
                </IconButton>
            </DialogTitle>
            <Divider />
            <InvoiceContainer invoice={invoice || {}} ref={invoiceRef} />
            <Divider />
            <DialogActions>
                <Button variant="contained" onClick={handlePrint}>چاپ</Button>
                <Box sx={{ flexGrow: 1 }}></Box>
                <Button onClick={onClose}>بستن</Button>
            </DialogActions>
        </Dialog>
    )
}

export default InvoicePrintDialog